:root {
  --star-inactive: url(images/star.svg);
  --star-active: url(images/star-active.svg);
}

body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: var(--star-inactive);
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1),
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(-n+5) {
  background-image: var(--star-active);
}
